a, a:visited {
    color: #fff;
}

input[type="text"],
input[type="password"],
button,
textarea {
  font-family: 'Nunito', sans-serif;
  font-size: 22px;
  padding: 0.25rem;
  border-radius: 0.5rem;
}

label {
  margin-top: 1rem;
  font-size: 18px;
}

button {
  /* padding: 0.5rem; */
  background-color: rgb(243, 82, 157);
    background: linear-gradient(-90deg, #02203c, #001528);
    border: none;
    border-radius: 40px;
    color: rgb(100, 8, 57);
    color: white;
    padding: 5px 20px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 0.5px;
    box-shadow:0px 0px 35px rgb(39, 51, 156);
    height: 50px;
    margin-top: 10%;
}

.button:hover{
  background: linear-gradient(45deg, #02203c, #001528);
  color: #fff;
  box-shadow:0px 0px 35px rgb(39, 51, 200);
}

.instructions {
    font-size: 0.75rem;
    border-radius: 0.5rem;
    background: #000;
    color: #fff;
    padding: 0.25rem;
    position: relative;
    bottom: -10px;
}

.instructions > svg {
    margin-right: 0.25rem;
}

.offscreen {
    position: absolute;
    left: -9999px;
}

.hide {
    display: none;
}

.valid {
    color: limegreen;
    margin-left: 0.25rem;
}

.invalid {
    color: red;
    margin-left: 0.25rem;
}

.errmsg {
    background-color: lightpink;
    color: firebrick;
    font-weight: bold;
    padding: 0.5rem;
    margin-bottom: 0.5rem;
}

.line {
    display: inline-block;
}
.nav-design-template {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-areas:
            "nav-design-left nav-design-center nav-design-right";
        background: linear-gradient(-90deg, #02203c, #001528);
        color: white;
        padding: 10px;
        align-items: center;
        margin-bottom: 1px;
    }

.nav-design-template {
    color: #8d96eb;
}


.nav-design-image {
    width: 10%; 
    height: auto; 
    border-radius: 50%;
}

.nav-design-left-row-one {
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.nav-design-left {
    grid-area: nav-design-left;
}

.nav-design-center {
    grid-area: nav-design-center;
    display: flex;
    justify-content: center;
    grid-gap: 25px;
    gap: 25px;
}

.nav-design-center a{
    text-decoration: none;
    color: white;
}

.nav-design-right {
    grid-area: nav-design-right;
    display: flex;
    justify-content: flex-end;
}

.nav-design-right a{
    text-decoration: none;
    color: white;
}

